  height: 100vh;
  height: 100svh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.lessons {
  background-image: url("https://diegobajetti.github.io/seg3125_p2_s5/images/img-1.jpg");
  background-position: center;
  background-size: cover;
