  font-size: 100px;
}

.challenges {
  background-image: url("https://diegobajetti.github.io/seg3125_p2_s5/images/img-2.jpg");
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.discuss {
  background-image: url("https://diegobajetti.github.io/seg3125_p2_s5/images/img-3.jpg");
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.sign-up {
  background-image: url("https://diegobajetti.github.io/seg3125_p2_s5/images/img-4.jpg");
  background-position: center;
  background-size: cover;
